import React from 'react'
import { Typewriter } from "react-simple-typewriter";

const Typeabout = () => {

  return (
    
      <>
        <Typewriter 
                  words={[
                    "Full Stack Web Developer",
                    "Problem Solver...",
                    "Fast Learner...",
                    "Coder...",
                  ]}
                  loop={500000}
                  cursor
                  cursorColor='text-cyan-300'
                  cursorStyle="|"
                  typeSpeed={90}
                  deleteSpeed={70}
                  delaySpeed={2000}
                />
      
        </>
        
   
  )
}

export default Typeabout