import React from "react";
import "../style/home.css";

import rashmiProfile from "../rashmiProfile.png";
import AOS from "aos";
import "aos/dist/aos.css";
import Typeabout from "./TypeAbout";
AOS.init();

const Home = () => {
  return (
    <div id="home" className="w-auto mx-auto bg-gradient-to-b from-black to bg-gray-800 md:h-screen text-white ">
      <div className="w-[90%] md:w-[90%] lg:w-[80%] flex justify-center lg:justify-around md:justify-around py-48 items-center  mx-auto flex-col lg:flex-row md:flex-row lg:pt-60 md:pt-60" >
        <div className=" flex flex-col gap-3 md:w-[50%] ">
          <h1 className="text-zinc-400 max-w-md text-2xl md:text-3xl flex  font-semibold">
            {" "}
            Hello ,
          </h1>
          <h1 className="text-zinc-400 max-w-md text-3xl md:text-4xl  font-semibold text-start">
            I'm
            <span className="max-w-md text-3xl md:text-5xl  font-semibold text-start">
              {" "}
              Rashmi Gautam
            </span>
          </h1>
          <div className="text-2xl flex md:text-3xl lg:text-5xl font-bold py-2 text-transparent text-left">
            <Typeabout />
          </div>

          <div class="custom-btn btn-6">
            <a
              className="flex"
              href={
                "https://drive.google.com/drive/folders/150ffx-dJ67yDtOMNeQM63qZDg9l84In0"
              }
              target="_blank"
            >
              <button class="custom-btn btn-6" type="button">
                RESUME
              </button>
            </a>
          </div>
        </div>
        <div  className="pt-16 lg:pt-0 md:pt-0 pl-14 lg:pl-0 md:pl-0">
          <div className="card">
          <img  src={rashmiProfile} alt="Rashmi" className="w-[100%]" />
        </div>
        </div>
      </div>
    </div>
  );
};
export default Home;
