import React from "react";
import "../style/about.css";
import AOS from "aos";
import "aos/dist/aos.css";
import gifabout from "../assets/gifabout.gif";
import Typeabout from "./TypeAbout";

AOS.init();

const About = () => {
  return (
    <div
      id="about"
      className="h-screen from-black to bg-gray-800 md:h-screen text-white "
    >
      
          <h1 className="text-3xl lg:text-5xl md:text-5xl pb-20 font-black text-zinc-200 underline underline-offset-8 ">About</h1>
      
      <div className="w-[90%] border-2 border-white m-auto rounded-3xl md:flex-row sm:flex-col">
        <div className="flex flex-col items-center justify-around w-[100%] py-10 lg:flex-row md:flex-row ">
          <div className="aboutimg w-[70%] lg:w-[40%] md:w-[40%] rounded-2xl">
            <img className="w-100% rounded-2xl"  src={gifabout} alt="aboutus" />
          </div>
          <div className="w-[100%] lg:w-[50%] md:w-[50%] pb-8 ">
            <div className="text-center text-2xl font-semibold text-violet-600 py-6 italic">
              <p >I am </p>
               <Typeabout/>
            </div>
            <p className="text-md flex text-sm lg:text-lg md:text-base text-center text-purple-200 font-sans md:font-seri ">
              {" "}
              A passionate React JS Front-End Web Developer, Dedicated to
              developing robust applications that interact with the front-end
              part of web applications. Extensive expertise in website
              architecture, design, and web design, and web API designs.
              Problem-solving mindset with a goal to optimize my application for
              better stability and speed. Love to work in a team.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
