import { Heading } from '@chakra-ui/react'
import React from 'react'

const Contact = () => {
  return (
    <div id='contact'>
       <div className='h-screen from-black to bg-gray-800 md:h-screen text-white py-56'>
        <i class="fa fa-chevron-left" aria-hidden="true"></i> Contact
        <span> / </span>
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </div>

    </div>
  )
}

export default Contact